import { createStore } from 'vuex';

export default createStore({
  state: {
    idSelected: "",
    debug: "true",
  },
  getters: {
    idSelected(status) {
      return status.idSelected;
    },
    debug(status) {
      return status.debug;
    },
  },
  mutations: {
    setIdSelected(status, idSelected) {
      status.idSelected = idSelected;
    },
  },
  actions: {
  },
  modules: {
  }
})
